import React from 'react';
import './Testnav.css'
import bxlGithub from '@iconify-icons/bx/bxl-github';
import logoutIcon from '@iconify-icons/fe/logout';
import { Icon, InlineIcon } from '@iconify/react';

const Testnav = () => {

    const onLogout = () => {
        window.location.href = "/"
    }


    return (

        <div id="navbarCss">
            <h3 className="brainCss">Brain</h3>
            <h3 className="passwordCss" >Password</h3>
            <button className="gihtubBtn" onClick={() => window.location.href = "https://github.com"}>Github <Icon icon={bxlGithub} width={24} height={24} /></button>
            <button className="logoutBtn" onClick={onLogout}>Logout <Icon icon={logoutIcon} width={24} height={24} /></button>
        </div>
    )
}

export default Testnav;