import React from 'react';
import PropTypes from 'prop-types';
import { Modal, ModalBody } from 'reactstrap';
import Lottie from 'react-lottie';

import animationData from '../../lotties/brain-eeg.json';


function LoadingPopup(props) {
    const defaultOptions = {
        loop: true,
        autoplay: true,
        animationData: animationData,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice"
        }
    };

    return (
        <Modal isOpen={props.isOpen} style={{ width: "300px" }}>
            <ModalBody >
                <h4 style={{textAlign:"center"}}>Uploading File</h4>
                <Lottie options={defaultOptions} height={200} width={200} speed={2}></Lottie>

            </ModalBody>
        </Modal>
    );
}

LoadingPopup.propTypes = {
    isOpen: PropTypes.bool.isRequired,
};

export default LoadingPopup;
