import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './App.css';
import Prediction from './pages/prediction/Prediction';
import TaskPerformStepper from './pages/taskPerform/TaskPerformStepper';

function App() {

  return (
    <Router>
      <div className="App">
        <Switch>
          <Route exact path={"/login.html/"} component={TaskPerformStepper} />
          <Route exact path={"/login.html/prediction"} component={Prediction} />
        </Switch>
      </div>
    </Router>


  );
}

export default App;
