import React, { useState } from 'react';
import Dropzone from 'react-dropzone';
import './UploadRecordings.css'
import { Container, Row } from 'reactstrap';


const UploadRecordings = (props) => {

    // recording stores the image file details
    const [recordings, setRecordings] = useState([])

    // Sets the image file when dragged and dropped in the dropping zone
    const handleDrop = acceptedFiles => {
        setRecordings(acceptedFiles)
        if (acceptedFiles.length == 1) {
            console.log(acceptedFiles)
            props.setFile(acceptedFiles[0])
        }
    }

    return (
        <Container style={{ marginTop: "2rem", fontFamily:"Poppins, sans-serif" }}>
            {/* <h1>Step 5: Upload the recordings</h1> */}
            <Dropzone
                onDrop={handleDrop}
                maxFiles={1}
            >
                {({ getRootProps, getInputProps }) => (
                    <div {...getRootProps({ className: "dropzone_upload_recording" })}>
                        <input {...getInputProps()} />
                        <span className="spam_upload">{"ðŸ“‚"}</span>
                        {
                            recordings.length === 0 ?
                                <p>Upload single EEG recording <br /> Drag'n'drop recordings, or click to select recording file</p> :
                                <p>{recordings.length + " "} Recordings Uploaded</p>
                        }
                    </div>
                )}
            </Dropzone>
        </Container>
    );
}

export default UploadRecordings;