import React from 'react';
import Lottie from 'react-lottie';
import animationData from '../../lotties/lf30_editor_3xwxjl1n.json';

const RecognitionAnimation = (props) => {
    const defaultOptions = {
        loop: true,
        autoplay: true,
        animationData: animationData,
        rendererSettings: {
            preserveAspectRatio: "xMidYMid slice"
        }
    };


    return (
        <div>
            <h1 style={{color:"white", textAlign:"center", fontWeight:"bold", fontFamily:"Poppins, sans-serif"}}>Authenticating</h1>
            <Lottie
                options={defaultOptions}
                height={500}
                width={500}
            />
        </div>
    );
}

export default RecognitionAnimation;
