import React, { useState, useEffect } from 'react';
import { Container } from 'reactstrap';
import { Bar, HorizontalBar } from 'react-chartjs-2';
import 'chartjs-plugin-datalabels';


const VerticalBarGraph = (props) => {
    const [ref, setRef] = useState(React.createRef)

    let subjectLabels = []

    for (let index = 0; index < props.avgSimilarityScores.length; index++) {
        subjectLabels.push(index.toString())

    }

    let colorArray = []
    
    const maxIndex = props.avgSimilarityScores.indexOf(Math.max(...props.avgSimilarityScores))

    for (let index = 0; index < props.avgSimilarityScores.length; index++) {
        colorArray.push('rgb(237, 88, 85)')

    }

    colorArray[maxIndex] = 'rgb(144, 238, 144)'

    const state = {
        labels: subjectLabels,
        datasets: [
            {
                label: 'Score',
                backgroundColor: colorArray,
                data: props.avgSimilarityScores,
            }

        ]
    }
    useEffect(() => {
        console.log(ref.current.props.data.datasets[0].backgroundColor)
    })
    return (
        <Container style={{ backgroundColor: "white", height: "50%" }}>
            <Bar
                ref={ref}
                data={state}
                options={{
                    title: {
                        display: true,
                        text: 'Mean Similarity Scores of All Subjects',
                        fontSize: 20,
                    },

                    plugins: {
                        datalabels: {
                            display: false,
                        }
                    },

                    legend: {
                        display: false,
                        position: 'right'
                    },

                    scales: {
                        xAxes: [{
                            gridLines: {
                                display: false
                            }
                        }],
                        yAxes: [{
                            ticks: {
                                beginAtZero: false,
                            },
                            gridLines: {
                                display: false
                            }
                        }]
                    }
                }}
            />
        </Container>
    );
}

export default VerticalBarGraph;
