import React, { useState } from 'react';
import { Row, Col, Container, Button } from 'reactstrap';
import UploadRecordings from '../../components/uploadRecordings/UploadRecordings';
import VerticalStepper from './VerticalStepper';
import { useHistory } from 'react-router-dom';
import { BASE_URL } from '../../constants';
import axios from 'axios';
import LoadingPopup from '../../components/Loader/LoadingPopup';
import Testnav from '../../components/navbar/Testnav';
import './TaskPerformStepper.css'


const TaskPerformStepper = (props) => {
    let history = useHistory();

    const [eegFile, setEegFile] = useState(null)
    const [spinner, showSpinner] = useState(false)


    const onClick = () => {
        console.log("predict")
        showSpinner(true)

        const eegRecordings = new FormData()
        if (eegFile === null) {
            alert("Upload a file")
        }

        eegRecordings.append("eeg_file", eegFile)

        axios.post(BASE_URL + "/predict", eegRecordings)
            .then((res) => {
                console.log(res)

                history.push({
                    pathname: "/login.html/prediction",
                    state: res.data
                }
                )
            })
            .catch((err) => {
                console.log(err)
                showSpinner(false)
            })

    }

    return (
        <>
            <Testnav />
            <Container style={{ fontFamily: "Poppins, sans-serif" }}>
                <h1 style={{ color: "white", textAlign: "center", fontWeight: "bold" }}>Upload Recording</h1>
                <Row>
                    <Col>
                        <VerticalStepper />
                    </Col>
                    <Col>
                        <UploadRecordings setFile={setEegFile} />
                        <Button onClick={onClick} className="fillButton fill">Predict</Button>
                        <LoadingPopup isOpen={spinner} />
                    </Col>
                </Row>
            </Container>
        </>
    );
}

export default TaskPerformStepper;