import {
    lightningChart,
    AxisTickStrategies,
    ColorRGBA,
    LUT,
    PalettedFill,
    transparentFill,
    SurfaceSeriesTypes3D,
    AxisScrollStrategies,
    SolidFill,
} from '@arction/lcjs'
import React, { Component } from 'react'

class Spectrogram3D extends Component {
    constructor(props) {
        super(props)
        // Generate random ID to use as the containerId for the chart and the target div id.
        this.chartId = Math.trunc(Math.random() * 100000).toString(10)
        this.dataSampleSize = 60

        // Length of data history.
        this.dataHistoryLength = 50


        // Setup PalettedFill for dynamically coloring Surface by Y coordinate.
        const lut = new LUT({
            steps: [
                { value: 15, color: ColorRGBA(4, 11, 125) },
                { value: 30, color: ColorRGBA(4, 130, 5) },
                { value: 60, color: ColorRGBA(132, 15, 4) },
                { value: 100, color: ColorRGBA(255, 255, 0) }
            ],
            interpolate: true
        })
        this.paletteFill = new PalettedFill({ lut, lookUpProperty: 'y' })

        this.testData = [32.09716882803229, 43.349310035336, 42.10847554868167, 45.81763619008004, 47.783003912966464, 54.503188379855715, 62.708732038232185, 53.2306868889928, 56.33187493092012, 54.62047661389083, 67.87000510662058, 66.14753547143053, 61.13022947896531, 58.05787303235591, 61.574241771686744, 49.56866851437859, 42.77196119454682, 42.60335895343615, 42.831640520131636, 33.57126738396935, 26.723746222048888, 22.72591371025781, 14.217158200510774, 8, 8, 8, 8, 8, 8, 8, 8.647330013165043, 19.669652041700466, 26.167051487286823, 35.96380116133894, 42.916906156067476, 45.34161367731428, 51.83479233046639, 45.65028434207095, 48.111038723844565, 45.359290026573206, 47.59692675217388, 41.06814556927395, 38.34760616336004, 28.86600953855033, 21.1985774417624, 10.571625402768191, 8, 8, 8, 8, 8, 8, 8, 8, 8, 8, 8, 8, 8, 8]
    }

    // Define a function which creates a chart.
    createChart() {

        this.chart3D = lightningChart().Chart3D({
            // theme: Themes.dark
        })
            .setTitle('Generating Spectrogram')
            .setTitleMarginTop(-10)
            .setTitleFont(f => f
                .setSize(40)
                .setWeight("bold")
            )
            .setBoundingBox({ x: 1, y: 1, z: 2 })
        this.chart3D.getDefaultAxisY()
            .setScrollStrategy(AxisScrollStrategies.expansion)
            .setInterval(0, 100)
            .setTitle('Power spectrum P(f)')

        this.chart3D.getDefaultAxisX()
            .setTitle('Frequency (Hz)')

        this.chart3D.getDefaultAxisZ()
            .setTitle('Time')
            .setTickStrategy(AxisTickStrategies.Empty)
            .setInterval(0, this.dataHistoryLength)

        // this.chart3D.setBackgroundFillStyle(transparentFill)
        this.chart3D.setBackgroundFillStyle(new SolidFill({
            // A (alpha) is optional - 255 by default
            color: ColorRGBA(0, 0, 0)
        }))

        // this.chart3D.setCameraLocation({ x: 1, y: 1, z: 2 })

        const rows = this.dataHistoryLength - 1
        const columns = this.dataSampleSize - 1

        this.surfaceGridSeries = this.chart3D.addSurfaceSeries({
            type: SurfaceSeriesTypes3D.Grid,
            rows,
            columns,
            start: { x: 0, z: this.dataHistoryLength },
            end: { x: this.dataSampleSize, z: 0 },
            pixelate: true
        })
            .setFillStyle(this.paletteFill)

        var chartDiv = document.getElementById("lcjs-auto-flexbox")
        chartDiv.style.width = "80%"
        chartDiv.style.height = "80%"
        chartDiv.style.margin = "auto"

        // Data generator
        setInterval(() => {
            let sampleData = this.testData
            for (let index = 0; index < 60; index++) {
                sampleData[index] = Math.random() * (100 - index);
            }
            this.surfaceGridSeries.addRow(1, 'y', [sampleData])
        }, 100)
    }

    componentDidMount() {
        // Chart can only be created when the component has mounted the DOM as
        // the chart needs the element with specified containerId to exist in the DOM
        this.createChart()

        const timer = setTimeout(() => {
            this.chart3D.dispose()
        }, 5900);
        return () => clearTimeout(timer);
    }

    // componentWillUnmount() {
    //     // "dispose" should be called when the component will unmount to free all the resources used by the chart.
    //     this.chart3D.dispose()
    // }

    render() {
        // render a component, which includes a div element. The chart will be created inside the div element.
        return (
            <div id={this.chartId} className='fill' style={{fontFamily:"Poppins, sans-serif"}}></div>
        )
    }
}

export default Spectrogram3D
