import React from 'react';
import { HorizontalBar } from 'react-chartjs-2';
import 'chartjs-plugin-datalabels';


const HorizontalBarGraph = (props) => {

    const state = {
        labels: ['Recording 1', 'Recording 2', 'Recording 3',
            'Recording 4', 'Recording 5', 'Recording 6'],
        datasets: [
            {
                label: 'Score',
                backgroundColor: 'rgb(69, 184, 255)',
                borderWidth: 2,
                data: props.similarityScores
            }
        ]
    }

    return (
        <div style={{ widht: "100%", height: "100%" }}>
            <HorizontalBar
                width={150}
                height={150}
                data={state}
                options={{
                    maintainAspectRatio: false,

                    title: {
                        display: true,
                        text: 'Similarity Scores Against Own Recording',
                        fontSize: 20
                    },

                    legend: {
                        display: false,
                        position: 'right'
                    },

                    plugins: {
                        datalabels: {
                            display: true,
                            color: 'white',
                            font: {
                                weight: 'bolder'
                            },
                        }
                    },

                    scales: {
                        xAxes: [{
                            ticks: {
                                beginAtZero: true
                            },
                            gridLines: {
                                display: false
                            }
                        }],
                        yAxes: [{
                            gridLines: {
                                display: false
                            }
                        }]
                    }
                }}
            />
        </div>
    );
}

export default HorizontalBarGraph;
