import React, { useState, useCallback, useEffect } from 'react'
import { useTransition, animated } from 'react-spring'
import ImageAnimation from './ImageAnimation';
import Spectrogram3D from './Spectrogram3D';
import RecognitionAnimation from './RecognitionAnimation';
import Testnav from '../../components/navbar/Testnav2';
import './Prediction.css'

const pages = [
    ({ style }) => <animated.div style={{ ...style }}><Spectrogram3D /></animated.div>,
    ({ style }) => <animated.div style={{ ...style }}><RecognitionAnimation /></animated.div>,
    ({ style, userDetails, similarityScores, avgSimilarityScores, subjectId }) => <animated.div style={{ ...style }}><ImageAnimation userDetails={userDetails} similarityScores={similarityScores} avgSimilarityScores={avgSimilarityScores} subjectId={subjectId}/></animated.div>,
]
// background: `linear-gradient(to right, #7b2ff7, #f107a3)`
// background: `linear-gradient(to left, #141e30, #243b55)`

const Prediction = (props) => {
    const [index, set] = useState(0)

    useEffect(() => {

        var count = 0
        const timer = setInterval(() => {
            if (count < 2) {
                set(state => (state + 1) % 3)
                count += 1
            }
        }, 6000);
        return () => clearInterval(timer);
    }, []);

    // const onClick = useCallback(() => set(state => (state + 1) % 3), [])
    const transitions = useTransition(index, {
        from: { opacity: 0, transform: 'translate3d(100%,0,0)' },
        enter: { opacity: 1, transform: 'translate3d(0%,0,0)' },
        leave: { opacity: 0, transform: 'translate3d(-50%,0,0)' },
    })

    return (
        <>
            <Testnav />
            <div className="simple-trans-main">
                {transitions((styleProps, item) => {
                    const Page = pages[item]
                    if (item < 2) {
                        return <Page key={item} style={styleProps} />
                    }
                    else {
                        console.log(props.location.state)
                        const similarityScores = props.location.state.data.user_similarity_scores
                        const userDetails = props.location.state.data.user
                        const avgSimilarityScores = props.location.state.data.average_similarity_scores
                        const subjectId = props.location.state.data.subject_idx
                        // const fakeScores = [0.9, 0.92, 0.88, 0.91, 0.97, 0.93]
                        // const fakeDetails = {
                        //     f_name: "John",
                        //     l_name: "Doe",
                        //     email: "john@gmail.com"
                        // }

                        return <Page key={item} style={styleProps} userDetails={userDetails} similarityScores={similarityScores} avgSimilarityScores={avgSimilarityScores} subjectId={subjectId}/>
                    }

                }
                )
                }
            </div>

        </>
    )
}

export default Prediction;

